/** Media Queries **/
html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Lato", sans-serif;
  font-size: 1.6rem;
  line-height: 1.8;
}

p {
  color: #000000;
  font-size: 2rem;
}

.contenedor {
  width: 70%;
  max-width: 120rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img,
picture {
  width: 100%;
  display: flex;
}

h1, h2, h3 {
  font-family: "Lato", sans-serif;
  margin: 2.5rem 0;
  font-weight: 300;
  text-align: center;
}

h1 {
  font-size: 3.8rem;
}

h2 {
  font-size: 3.4rem;
}

h3 {
  font-size: 3rem;
}

h4 {
  font-size: 2.6rem;
}

.seccion {
  margin-top: 2.5rem;
  margin-bottom: 2.5rem;
}

.contenido-anuncio {
  max-width: 100%;
  flex-wrap: wrap;
}

.alinear-derecha {
  display: flex;
  justify-content: flex-end;
}

.w-100 {
  width: 100%;
}

.boton-azul-block {
  background-color: #1e40af;
  color: #FFFFFF;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
  display: block;
  border: none;
  border-radius: 12px;
}
.boton-azul-block:hover {
  background-color: #1a3899;
  cursor: pointer;
}

.boton-azul {
  background-color: #1e40af;
  color: #FFFFFF;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
  display: inline-block;
  border: none;
  border-radius: 12px;
}
.boton-azul:hover {
  background-color: #1a3899;
  cursor: pointer;
}

.boton-rojo-block {
  background-color: rgb(189, 7, 7);
  color: #FFFFFF;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
  display: block;
  border: none;
  border-radius: 12px;
  width: 100%;
}
.boton-rojo-block:hover {
  background-color: #a40606;
  cursor: pointer;
}

.boton-verde-block {
  background-color: #71B100;
  color: #FFFFFF;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
  display: block;
  border: none;
  border-radius: 12px;
}
.boton-verde-block:hover {
  background-color: #619800;
  cursor: pointer;
}

/** UI **/
.webp .inicio {
  background-color: black;
}

.contenido-header {
  display: flex;
  flex-direction: column-reverse;
  justify-content: space-between;
}

.header {
  background-color: #000000;
  padding: 1rem 0 0 0;
}
.header.inicio {
  background-position: center center;
  background-size: cover;
}
@media (min-width: 768px) {
  .header.inicio {
    min-height: 16rem;
  }
}

.titulo-logo {
  color: white;
  font-size: 6rem;
  font-family: Montserrat, sans-serif;
}

.footer {
  background-color: #FFFFFF;
  padding: 4rem 0;
}
.footer .copyright {
  color: #000000;
  text-align: center;
}

.derecha {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
}
@media (min-width: 768px) {
  .derecha {
    align-items: flex-end;
    text-align: center;
  }
}
@media (min-width: 768px) {
  .derecha {
    margin-top: 0;
  }
}

.barra {
  color: black;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-top: 1rem;
  margin-bottom: 2rem;
}
@media (min-width: 768px) {
  .barra {
    flex-direction: row;
    justify-content: space-between;
  }
}

.navegacion {
  visibility: hidden;
  opacity: 0;
  height: 0;
  transition: visibility 0.3s ease-in-out, opacity 0.5s linear, height 0.5s linear;
}
@media (min-width: 768px) {
  .navegacion {
    display: flex;
    align-items: center;
    opacity: 1;
    visibility: visible;
  }
}
.navegacion a {
  color: #FFFFFF;
  text-decoration: none;
  font-size: 2rem;
  display: block;
  text-align: center;
}
@media (min-width: 768px) {
  .navegacion a {
    margin-right: 2rem;
    font-size: 2rem;
    text-align: center;
    align-items: center;
  }
  .navegacion a:last-of-type {
    margin-right: 0;
  }
}
.navegacion a:hover {
  color: #1e40af;
}

/** Menu de hamburguesa **/
@media (min-width: 768px) {
  .mobile-menu {
    display: none;
  }
}
.mobile-menu img {
  width: 5rem;
}

.mostrar {
  visibility: visible;
  opacity: 1;
  height: auto;
}

@media (min-width: 768px) {
  .iconos-nosotros {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(3, 1fr);
  }
}
.icono {
  fill: white;
  text-align: center;
}
.icono img {
  fill: white;
  height: 12rem;
}
.icono h3 {
  text-transform: uppercase;
}

@media (min-width: 768px) {
  .contenedor-anuncios {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(3, 1fr);
  }
}
.anuncio {
  background-color: #eeeeee;
  border: 1px solid #c8c8c8;
  margin-bottom: 2rem;
}
.anuncio .contenido-anuncio {
  padding: 2rem;
}
.anuncio h3, .anuncio p {
  margin: 0;
}

.precio {
  color: black;
  font-weight: 700;
  font-size: 3rem;
}

.alinear-derecha {
  margin-right: 2rem;
}

@media (min-width: 768px) {
  .alinear-derecha {
    display: flex;
    justify-content: flex-end;
    margin-right: 0;
  }
}
.resumen-producto {
  text-align: center;
  padding: 0 4rem;
  font-size: x-large;
}

.formulario {
  border: none;
  margin-top: 4rem;
}
.formulario .informacion {
  display: flex;
  flex-wrap: wrap;
}
.formulario .izquierda {
  width: 100%;
}
.formulario .informacionDerecha {
  width: 100%;
}
.formulario label {
  font-weight: 700;
  text-transform: uppercase;
  display: block;
}
.formulario input:not([type=submit]),
.formulario textarea,
.formulario select {
  padding: 1rem;
  display: block;
  width: 100%;
  background-color: #FFFFFF;
  border: 1px solid #e1e1e1;
  border-radius: 1rem;
  margin-bottom: 2rem;
}
.formulario textarea {
  height: 25rem;
}
.formulario input[type=radio] {
  margin: 0;
  width: auto;
}
.formulario .boton-azul {
  display: flex;
  flex-direction: column;
}

@media (min-width: 768px) {
  .informacion {
    display: flex;
    flex-wrap: wrap;
  }
  .informacion .izquierda {
    width: 45%;
  }
  .informacion .informacionDerecha {
    width: 45%;
  }
}
/** Paginas Internas **/
@media (min-width: 768px) {
  .contenido-nosotros {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(2, 1fr);
  }
}

.texto-nosotros blockquote {
  font-weight: 900;
  font-size: 2.2rem;
  margin: 3rem 0 0 0;
  padding-left: 1rem 0 3rem 0;
}

.image-nosotros {
  box-shadow: 10px 10px 30px rgba(0, 0, 0, 0.5);
  width: 100%;
  height: 78%;
}

.alerta {
  padding: 0.5rem;
  text-align: center;
  color: #FFFFFF;
  margin: 1rem 0;
  border-radius: 12px;
}
.alerta.error {
  background-color: rgb(189, 7, 7);
}
.alerta.exito {
  background-color: #71B100;
}

table.productos {
  margin-top: 4rem;
  width: 100%;
  border-spacing: 0;
  text-align: center;
  align-items: center;
}
table.productos thead {
  background-color: #1e40af;
}
table.productos thead th {
  color: #FFFFFF;
  padding: 2rem 1rem;
  border: 1px black solid;
}
table.productos .imagen-tabla {
  width: 10rem;
}

.imagen-small {
  width: 20rem;
}